



























export default {
	data() {
		return {
			mobile:'',
			showPassword: true,
			loading:false,
			tipcontent:'',
			is_sms:true,
			Loop:'',
			downtime:60,
			verifyCode:'',
			smsCode:'',
			fsbtn:'发送验证码'
		}
	},
	onLoad(e) {
		if(e.mobile){
			this.mobile = e.mobile;
		}else{
			uni.navigateTo({
				url: '/pages/user/set/set'
			})
		}
	},
	onPullDownRefresh() {
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onNavigationBarButtonTap(e) {
	},
	methods: {
		goDetailPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
			this.showPopupTop = false;
			this.showPopupBottom = false;
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.popType = 'middle';
			this.showPopupMiddle = true;
		},
		changePassword: function () {
			this.showPassword = !this.showPassword;
		},
		formSubmit: function (e) {
			const password = e.detail.value.password;
			const smsCode = e.detail.value.smsCode;
			const verifyCode = this.verifyCode;
			
			if(smsCode ==='' ){
				uni.showModal({
					content:'请填写验证码',
					showCancel:false
				})
				return false;
			}
			
			if(verifyCode ==='' ){
				uni.showModal({
					content:'请先获取验证码',
					showCancel:false
				})
				return false;
			}
			
			if(password ==='' ){
				uni.showModal({
					content:'请填写6~18位字符新密码',
					showCancel:false,
				})
				return false;
			}
			const that = this;
			try {
				const openid = this.$store.state.openid;
				const sessionKey = this.$store.state.sessionKey;
				this.$http.post(this.websiteUrl+'/api/api/uppassword',{openid,sessionKey,password,smsCode,verifyCode},(res) => {
					if(res.data.code==1){
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:()=>{
								this.$store.commit('logout');
								uni.reLaunch({
									url: '/pages/tabBar/user/user'
								})
							}
						})
						
						//uni.navigateBack();
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:function() {
								that.loading = false;
							}
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false,
					success:function() {
						that.loading = false;
					}
				})
			}
			
			//console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		},
		sendSMS: function (e) {
			if(this.is_sms){
				this.is_sms = false;
				const openid = this.$store.state.openid;
				const sessionKey = this.$store.state.sessionKey;
				const is_user = 1;
				const getPass = 1;
				try {
					this.$http.post(this.websiteUrl + '/api/api/sendSMS', {
						openid,
						sessionKey,
						is_user,
						getPass
					}, (res) => {
						if (res.data.code == 1) {
							this.downtime =60;
							this.dateDown();
							this.verifyCode = res.data.result;
						}else{
							uni.showModal({
								content: res.data.msg,
								showCancel: false
							})
							this.is_sms = true;
						}
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false
					})
					this.is_sms = true;
				}	
			}	
		},
		dateDown: function () {
			const that = this;
			this.fsbtn='重新发送('+this.downtime+')';
			this.Loop = setInterval(function() {
				if(that.downtime == 0){
					that.fsbtn='发送验证码';
					that.is_sms = true;
					clearInterval(that.Loop);
					return;
				}
				that.downtime--;
				that.fsbtn='重新发送('+this.downtime+')';
			}.bind(this), 1000);
			if(this.is_sms){
				clearInterval(this.Loop);
			}
		}
	},
	components: {
		
	}
}
